.printer-app {
   background: url(../../img/hero.jpg) top;
   background-size: 100% 100%;
   height: 100vh;
   margin: 0;
   padding: 20px;

   .printer-status {
      margin: auto auto 20px auto;
      padding: 20px;
      text-align: center;
      background: var(--color-white);
      padding: var(--card-padding);
      border-radius: var(--card-border-radius);
      margin-top: 1rem;
      box-shadow: var(--box-shadow);
      transition: all 300ms ease;
      gap: 20px;
      width: 400px;
      align-items: center;

      &:hover {
         box-shadow: none;
         transition: all 300ms ease;
      }

      button {
         margin: 0;
      }
   }

   .printer-list {
      gap: 10px;
      list-style: none;
      flex-wrap: wrap;
   }

   .printer-preview {
      cursor: pointer;
      min-width: 200px;
      min-height: 100px;
      color: whitesmoke;
      border-radius: var(--card-border-radius);
      margin: 5px;
      text-align: center;
      padding: 20px;
      box-shadow: var(--box-shadow);
      transition: all 300ms ease;

      &:hover {
         font-size: 18px;
         box-shadow: none;
         transition: all 300ms ease;
      }

      p {
         margin: 10px 0 0 0;
      }

      button {
         margin: 0;
      }

      .close-btn {
         display: flex;
         align-content: center;
         justify-content: center;
         width: 26px;
         height: 26px;
         align-self: flex-end;

         h3 {
            font-size: 15px;
            margin: 0;
         }
      }

      input {
         height: 40px;
         border-radius: 10px;
         border: 1px solid gray;
         width: 100%;
         padding: 10px;
      }
   }

   .printer-preview span {
      color: gold;
   }
}